*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    // outline: 1px red solid;
}

h1,h2,h3,h4,h5,p{
    margin: 0;
    padding: 0;
}

html {
    // scroll-behavior: smooth;
  }

body{
    font-family: 'Montserrat', sans-serif;

    // position: relative !important;
    header{
        background: url(../img/head-img.png) 50% 50% / cover;
        background-attachment: fixed;
        
        .menu{
            width: 100%;
            position: fixed;
            display: flex;
            justify-content: space-between;
            align-items: center;
            max-height: 65.9px;
            padding: 20px 40px;
            background: rgba(0, 0, 0, 0.404);
            .logo{
                width: 20%;
                img{
                    width: 100px;
                    height: 23px;
                }
            }
            
            .navs{
                width: 80%;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                a{
                    font-family: 'Gilroy';
                    font-weight: 600;
                    font-size: 16px;
                    text-decoration: none;
                    color: rgb(255, 255, 255);
                    margin: 0 15px;
                    transition: 0.2s;
                }

                a:hover{
                    color: rgb(0, 162, 255);
                    font-size: 19px;
                }
            }
        }

        .container{
            

            .head_txt{
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                padding: 230px 0 115px 0;

                .head_lines{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    text-align: center;
                    margin-bottom: 20px;
                    .hl{
                        width: 150px;
                        height: 1px;
                        background: rgb(255, 208, 0);
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }

                    .hl:first-child{
                        animation: hl1 infinite linear 2s;
                    }
                    .hl:last-child{
                        animation: hl2 infinite linear 2s;
                    }

                    h3{
                        font-weight: 500;
                        font-size: 18px;
                        margin: 0 20px 0 20px !important;
                        margin-bottom: 20px;
                        color: white;
                    }
                }
                
                .title{
                    font-family: 'Montserrat', sans-serif;
                    font-weight: 900;
                    font-size: 40px;
                    line-height: 64px;
                    color: white;
                    text-align: center;
                    margin-bottom: 50px;
                }

                .title_div{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin-bottom: 100px;
                    .title_a{
                        width: 200px;
                        height: 43px;
                        background: rgb(0, 132, 255);
                        border-radius: 50px;
                        color: white;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-decoration: none;
                        padding-bottom: 5px;
                        font-weight: 500;
                        line-height: 20px;
                        font-size: 16px;
                        box-shadow: 0px 0px 15px 1px rgb(0, 132, 255);
                        animation: shd infinite linear 4s;
                        transition: 0.5s;
                        margin-left: 60px;
                    }

                    .title_a:hover{
                        background: white;
                        color: black;
                    }

                    .title_arrow{
                        opacity: 0;
                        transition: 0.5s;
                        // margin-left: -50px;
                        img{
                            width: 50px;
                            height: 50px;
                        }
                    }
                }

                
                
                .scrollDown{
                    animation: scroll_sqew  infinite linear 3s;
                    img{
                        animation: scroll_rotate infinite linear 3s;
                        width: 50px;
                        height: 50px;
                    }
                }


            }

        }
    }

    main{
        .web{
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 120px 0;
            .container{
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                h2{
                    font-family: 'Montserrat', sans-serif;
                    font-style: normal;
                    text-align: center;
                    font-weight: bold ;
                    font-size: 36px;
                    line-height: 53px;
                    margin-bottom: 50px;
                }

                p{
                    font-family: Montserrat;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 25px;
                    text-align: center;
                    font-style: normal;
                    margin-bottom: 70px;
                }

                .web_div{
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    margin-bottom: 55px;
                    .info_div{
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin-left: 15px;
                        margin-right: 15px;
                        .div_icon{
                            margin-right: 20px;
                            justify-content: center;
                            align-items: center;
                            min-width:110px;
                            height: 110px;
                            border-radius: 50%;
                            border: 1px solid #E0E0E0;
                            display: flex;
                            .icon_div{
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                width: 90px;
                                height: 90px;
                                border-radius: 50%;
                                border: 1px solid #4985FF;
                                img{
                                    width: 40px;
                                    height: 40px;
                                }
                            }
                        }
                    }
                    .info_text{
                        display: flex;
                        justify-content: center;
                        align-items: flex-start;
                        flex-direction: column;
                        h3{
                            
                            font-family: Montserrat;
                            font-style: normal;
                            font-weight: 600;
                            font-size: 18px;
                            line-height: 22px;
                        }
                        p{
                            text-align: left;
                            margin-bottom: 0px;
                            font-family: Montserrat;
                            font-weight: normal;
                            font-size: 14px;
                            line-height: 22px;
                            font-style: normal;
                            color: #999999;
                        }
                    }
                }

                a{
                    width: 200px;
                        height: 43px;
                        background: rgb(0, 132, 255);
                        border-radius: 50px;
                        color: white;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-decoration: none;
                        padding-bottom: 5px;
                        font-weight: 500;
                        line-height: 20px;
                        font-size: 16px;
                        box-shadow: 0px 0px 15px 1px rgb(0, 132, 255);
                        animation: shd2 infinite linear 4s;
                        transition: 0.5s;
                        
                }

                a:hover{
                    background: rgb(255, 255, 255);
                    color: black
                }
            }
        }
    }
}


@keyframes scroll_rotate {
    0%{
        transform: rotate(0deg);
        transform: translateY(0px);
    }
    25%{
        transform: translateY(-40px);
        transform: rotate(90deg);

    }
    50%{
        transform: translateY(0px);
        transform: rotate(180deg);
    }
    75%{
        transform: translateY(40px);
        transform: rotate(270deg);
    }
    100%{
        transform: rotate(360deg);
        transform: translateY(0px);
    }
}

@keyframes scroll_sqew {
    0%{
        transform: translateY(0px);
    }
    25%{
        transform: translateY(-40px);

    }
    50%{
        transform: translateY(0px);
    }
    75%{
        transform: translateY(40px);
    }
    100%{
        transform: translateY(0px);
    }
}


@keyframes hl1{
    0%{
        background: rgb(255, 208, 0);
    }
    
    50%{
        background: rgb(0, 132, 255);
    }

    100%{
        background: rgb(255, 208, 0);
    }
}

@keyframes hl2{
    0%{
        background: rgb(0, 132, 255);
    }
    
    50%{
        background: rgb(255, 208, 0);
    }

    100%{
        background: rgb(0, 132, 255);
    }
}

@keyframes shd {
    0%{
        box-shadow: 0px 0px 15px 1px rgb(0, 132, 255);
    }
    25%{
        box-shadow: 0px 0px 15px 1px rgb(255, 255, 255);
    }
    75%{
        box-shadow: 0px 0px 15px 1px rgb(255, 255, 255);
    }
    100%{
        box-shadow: 0px 0px 15px 1px rgb(0, 132, 255);
    }
}

@keyframes shd2 {
    0%{
        box-shadow: 0px 0px 15px 1px rgb(255, 255, 255);
    }
    25%{
        box-shadow: 0px 0px 10px 0px rgb(0, 132, 255);
    }
    75%{
        box-shadow: 0px 0px 15px 1px rgb(0, 132, 255);
    }
    100%{
        box-shadow: 0px 0px 15px 1px rgb(255, 255, 255);
    }
}